

















































import {
  computed, defineComponent, onMounted, onUnmounted, PropType, ref,
} from '@vue/composition-api';
import { useWindowScrollSettings } from '@/app/shared/client/logic/scroll-settings';
import { useTileOffsetSettings } from '@/app/shared/client/logic/offset-settings';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import {
  StyleKeyValue,
  tileStyleSelector,
  generalStyles,
  textStyles,
  imageStyles,
  imageOverlayStyles,
} from '@/app/website/client/logic/tile-style';
import {
  getImageStyleSelector,
  getImageSet,
} from '@/app/website/client/logic/image-style';
import {
  ImageTextTileContent,
  ImageTextTileDesign,
  ImageTextTileLayout,
} from '@/app/shared/client/type/tile/image-text-tile';
import {
  ImageSettings,
} from '@/app/shared/client/type/settings/image-settings';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import {
  BackgroundType,
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import { Breakpoints, ViewportSettings } from '@/app/shared/client/type/settings/viewport-settings';
import { checkIfTileShown } from '@/app/website/client/logic/appearance-behaviour';
import SubtitleRightDefaults from '@/app/website/client/defaults/image-text/subtitle-right.json';
import TitleLeftDefaults from '@/app/website/client/defaults/image-text/title-left.json';
import ImageLeftDefaults from '@/app/website/client/defaults/image-text/image-left.json';
import ImageRightDefaults from '@/app/website/client/defaults/image-text/image-right.json';
import CenterDefaults from '@/app/website/client/defaults/image-text/center.json';
import SideTitleDefaults from '@/app/website/client/defaults/image-text/side-title.json';
import {
  ImageTextTileLayoutDefaults,
} from '@/app/shared/client/type/defaults/image-text-tile-defaults';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import { State } from '@/app/website/client/store/state';
import { ImageData } from '@/app/shared/client/type/image';
import {
  getImageInUploadingState,
} from '@/app/website/client/logic/image-uploading';
import FontLoader from '@/app/website/client/view/component/FontLoader.vue';
import TileTitle from '@/app/website/client/view/component/TileTitle.vue';

  interface ImageTextTileProps {
    tileId: string;
    content: ImageTextTileContent;
    design: ImageTextTileDesign;
    viewportSettings: ViewportSettings;
  }

function getDefaults(
  layout: ImageTextTileLayout | undefined,
): ImageTextTileLayoutDefaults | undefined {
  switch (layout) {
    case 'SUBTITLE_RIGHT':
      return SubtitleRightDefaults as ImageTextTileLayoutDefaults;
    case 'TITLE_LEFT':
      return TitleLeftDefaults as ImageTextTileLayoutDefaults;
    case 'IMAGE_LEFT':
      return ImageLeftDefaults as ImageTextTileLayoutDefaults;
    case 'IMAGE_RIGHT':
      return ImageRightDefaults as ImageTextTileLayoutDefaults;
    case 'CENTER':
      return CenterDefaults as ImageTextTileLayoutDefaults;
    case 'SIDE_TITLE':
      return SideTitleDefaults as ImageTextTileLayoutDefaults;
    default:
      return undefined;
  }
}

function computedLayoutClass(layout: ImageTextTileLayout | undefined): string {
  switch (layout) {
    case 'SUBTITLE_RIGHT':
      return 'subtitle-right';
    case 'TITLE_LEFT':
      return 'title-left';
    case 'IMAGE_LEFT':
      return 'image-left';
    case 'IMAGE_RIGHT':
      return 'image-right';
    case 'CENTER':
      return 'center';
    case 'SIDE_TITLE':
      return 'side-title';
    default:
      return 'side-title';
  }
}

function tileStyles(
  design: ImageTextTileDesign,
  imageData?: ImageData,
): StyleKeyValue[] {
  const styles: StyleKeyValue[] = [
    ...generalStyles(design.general),
    ...textStyles('title', design.title),
    ...textStyles('subtitle', design.subtitle),
    ...textStyles('description', design.description),
    ...imageOverlayStyles('image', design.image),
    ...imageStyles('image', imageData),
  ];

  return styles;
}

function checkIfTileTransparent(
  design: ImageTextTileDesign,
  siteSettings?: GlobalDesign,
): boolean {
  const tileBackground = design.general?.background;
  const siteBackground = siteSettings?.color?.background;
  switch (tileBackground?.type) {
    case BackgroundType.GRADIENT:
      return (tileBackground?.gradient?.fromColor?.hsl?.a ?? 0) === 0
        && (tileBackground?.gradient?.toColor?.hsl?.a ?? 0) === 0;
    case BackgroundType.SOLID:
      return (tileBackground?.solid?.color?.hsl?.a ?? 0) === 0
        || (siteBackground?.hsl?.h === tileBackground?.solid?.color?.hsl?.h
          && siteBackground?.hsl?.s === tileBackground?.solid?.color?.hsl?.s
          && siteBackground?.hsl?.l === tileBackground?.solid?.color?.hsl?.l
          && siteBackground?.hsl?.a === tileBackground?.solid?.color?.hsl?.a
        );
    default:
      return false;
  }
}

const ImageTextTile = defineComponent({
  components: {
    FontLoader,
    TileTitle,
  },
  props: {
    tileId: {
      required: true,
      type: String,
    },
    content: {
      required: true,
      type: Object as PropType<ImageTextTileContent>,
    },
    design: {
      required: true,
      type: Object as PropType<ImageTextTileDesign>,
    },
    viewportSettings: {
      required: true,
      type: Object as PropType<ViewportSettings>,
    },
    isHeadTile: {
      required: true,
      type: Boolean,
    },
  },
  setup(props: ImageTextTileProps, { attrs, root, emit }) {
    const tileRef = ref<HTMLElement>();

    const {
      windowScrollTop,
      subscribeOnWindowScrollChange,
    } = useWindowScrollSettings();

    const {
      tileOffsetTop,
      tileOffsetHeight,
      subscribeOnTileOffsetChange,
    } = useTileOffsetSettings(tileRef);

    const layoutClass = computed(() => `ins-tile--${computedLayoutClass(props.design.layout)}`);
    const defaults = computed(() => getDefaults(props.design.layout));

    const design = computed<ImageTextTileDesign>(() => {
      const userValue = props.design;
      const general: GeneralDesignSettings = {
        ...defaults.value?.general,
        ...userValue.general,
        background: {
          ...defaults.value?.general?.background,
          ...userValue.general?.background,
          solid: {
            ...defaults.value?.general?.background?.solid,
            ...userValue.general?.background?.solid,
          },
          gradient: {
            ...defaults.value?.general?.background?.gradient,
            ...userValue.general?.background?.gradient,
          },
        },
      };

      const title: TextSettings = {
        ...(defaults.value?.title as TextSettings),
        ...userValue.title,
      };

      const subtitle: TextSettings = {
        ...(defaults.value?.subtitle as TextSettings),
        ...userValue.subtitle,
      };

      const description: TextSettings = {
        ...(defaults.value?.description as TextSettings),
        ...userValue.description,
      };

      const image: ImageSettings = {
        ...defaults.value?.image,
        ...userValue.image,
        overlay: {
          ...defaults.value?.image?.overlay,
          ...userValue.image?.overlay,
          solid: {
            ...defaults.value?.image?.overlay?.solid,
            ...userValue.image?.overlay?.solid,
          },
          gradient: {
            ...defaults.value?.image?.overlay?.gradient,
            ...userValue.image?.overlay?.gradient,
          },
        },
      };

      return {
        layout: userValue.layout,
        general,
        title,
        subtitle,
        description,
        image,
      };
    });

    const isTileShowRef = ref(false);
    const isTileShown = computed(() => checkIfTileShown(
      isTileShowRef,
      props.viewportSettings,
      windowScrollTop.value,
      tileOffsetTop.value,
      tileOffsetHeight.value,
    ));

    const fonts = computed(() => [
      design.value.title?.font,
      design.value.subtitle?.font,
      design.value.description?.font,
    ]);

    const styleSelector = computed(() => {
      const styles = tileStyles(
        design.value,
        props.content.image,
      );
      return tileStyleSelector(`#${attrs.id}`, styles);
    });

    const hasTitle = computed(() => {
      const isTitleVisible = design.value.title?.visible;
      const hasTitleContent = isNotNullOrEmpty(props.content.title);
      return isTitleVisible && hasTitleContent;
    });

    const hasSubtitle = computed(() => {
      const isSubtitleVisible = design.value.subtitle?.visible;
      const hasSubtitleContent = isNotNullOrEmpty(props.content.subtitle);
      return isSubtitleVisible && hasSubtitleContent;
    });

    const hasDescription = computed(() => {
      const isDescriptionVisible = design.value.description?.visible;
      const hasDescriptionContent = isNotNullOrEmpty(props.content.description);
      return isDescriptionVisible && hasDescriptionContent;
    });

    const hasImage = computed(() => {
      const isImageVisible = design.value.image?.visible;
      const hasImageContent = props.content.image !== undefined;

      return isImageVisible && hasImageContent;
    });

    const hasTransparentBackground = computed(() => checkIfTileTransparent(
      design.value,
      root.$store.state.siteSettings?.design,
    ));

    const uploadingImage = computed(() => {
      const { imageListInUploadingState } = root.$store.state as State;
      return getImageInUploadingState(imageListInUploadingState, props.tileId, 'image');
    });
    const isUploadingImage = computed(() => uploadingImage.value !== undefined);

    const imageStyleSelector = computed(() => {
      const imageSet = getImageSet(
        root.$store.state.context,
        uploadingImage.value,
        props.content.image,
      );

      const styles: string[] = [];
      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__picture`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url600,
          image2x: imageSet?.url1200,
          backgroundColor: imageSet?.backgroundColor,
        },
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id} .ins-tile__picture`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url1200,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_S,
      ));

      styles.push(getImageStyleSelector(
        `#${attrs.id}.ins-tile--subtitle-right .ins-tile__picture,
         #${attrs.id}.ins-tile--title-left .ins-tile__picture,
         #${attrs.id}.ins-tile--center .ins-tile__picture`,
        {
          thumbnail: imageSet?.url200,
          image1x: imageSet?.url2000,
          image2x: imageSet?.url2000,
          backgroundColor: imageSet?.backgroundColor,
        },
        Breakpoints.FROM_L,
      ));

      return styles.join('');
    });

    onMounted(() => {
      emit('tile-loaded');
      subscribeOnWindowScrollChange();
      subscribeOnTileOffsetChange();
    });

    onUnmounted(() => {
      emit('tile-unloaded');
    });

    return {
      tileRef,
      layoutClass,
      fonts,
      styleSelector,
      imageStyleSelector,
      hasTitle,
      hasSubtitle,
      hasDescription,
      hasImage,
      hasTransparentBackground,
      isUploadingImage,
      isTileShown,
    };
  },
});

export default ImageTextTile;
